import React from "react";
import TopRatedMovie from './TopRatedMovie';

class TopRatedComponent extends React.Component {
    render() {
        return (
            <li>
              {Object.keys(this.props.movieDetails).map(key => 
                <TopRatedMovie
                  key={key}
                  movieDetails={this.props.movieDetails[key]}
                 />
              )}
            </li>
        );
    }
}

export default TopRatedComponent;
